<template>
	<div class="Orderconfirm-page">
		<div class="container">
			<div class="row">
				<div class="col-md-12">
				<h1>Thank you for your order!</h1>
				<p>Confirmation number: <span v-if="order_id" v-text="order_id"></span></p>
				<p>You will receive a confirmation email soon. <br>You can view the order details from your account dashboard.</p>
                <router-link to="/account" class="mayaccount-btn">My account</router-link>
				<router-link to="/" class="backTohome">Back to homepage</router-link>
			</div>
			</div>
		</div>
	</div>
</template>
<script>
export default {
    name:'thankyou',
    props:['order'],
    data() {
        return {
            order_id:''
        }
    },
    beforeMount() {
        if((!this.order || !this.order.id || typeof(this.order)==='undefined' ) && !this.$route.query.od ){
            this.$router.push('/account').catch({});
        }else{
            this.order_id=this.$route.query.od? this.$route.query.od : this.order.id
        }

    },
}
</script>

